import NavBar from "../FrontPage/NavBar";
import * as React from 'react';
import { useNavigate } from "react-router-dom";
import { useRef, useState } from "react";
import { TextField, Button } from "@mui/material";
import '../style/home.css'

function getToken(exp: number): Boolean {
    const data1 = localStorage.getItem('token');
    if (!data1) {
        return false;
    } else {
        const data = JSON.parse(data1);
        if (data.time + exp < new Date().getTime()) {
            return false;
        } else {
            return true;
        }
    }
}

function useComponentWillMount(func: Function) {
    const ref = useRef(true);
    if (ref.current) {
        func();
        ref.current = false;
    }
}

function uploadDataFunc(data:any) {
    fetch('http://localhost:1234/api/uploadBlog', {
        method: 'post',
        headers: {
            'Content-Type': 'application/json;charset=utf-8'
        },
        body: data
    });
}

export default function Home() {
    const navigate = useNavigate();
    const [title, setTitle] = useState('');
    const [time, setTime] = useState('');
    const [content, setContent] = useState('');
    const [tag, setTag] = useState('');
    if (!getToken(1000 * 60 * 60 * 24)) {
        //Êú¨Êù•ÊÉ≥ÂÅöÂà∞Â¶ÇÊûúÊ≤°ÊúâÁôªÂΩïÂÖàÂºπÁ™óÂÜçË∑≥ËΩ¨ÂõûÈ¶ñÈ°µ
        //‰ΩÜÊòØÁî±‰∫éuseNavigate()ÊúâÁÇπÂ•áÊÄ™?
        //Âú®component will mountÁöÑÊó∂ÂÄôÂà§Êñ≠ÁöÑËØù‰∏ç‰ºöË∑≥ËΩ¨ÂõûÂéªÔºåÊé®ÊµãÊòØË∑ØÁî±ÈáçÂÆöÂêëÁöÑ‰ºòÂÖàÁ∫ßÈóÆÈ¢òÔºü
        //ÂèØËÉΩÊòØÂÖàÈáçÂÆöÂêëÂà∞‰∫Ü'/'ÔºåÂÜçÈáçÂÆöÂêëÂõû‰∫Ü'/home'?
        //ËÄå‰∏îalert‰ºöÂá∫Áé∞‰∏§Ê¨°ÔºåÁ¨¨‰∫åÊ¨°Âá∫Áé∞ÂêéÊâç‰ºöË∑≥ËΩ¨ÂõûÈ¶ñÈ°µ(Êåá‰∏çÂä†willmountÈí©Â≠êÁöÑÊó∂ÂÄô)
        navigate('/');
    }

    function handleTitle(e: React.FocusEvent<HTMLInputElement>): void {
        setTitle(e.target.value);
    }

    function handleTime(e: React.FocusEvent<HTMLInputElement>): void {
        setTime(e.target.value);
    }

    function handleContent(e: React.FocusEvent<HTMLTextAreaElement>): void {
        setContent(e.target.value);
    }

    function handleTag(e: React.FocusEvent<HTMLInputElement>):void {
        setTag(e.target.value);
    }

    function handleSubmit() {
        const uploadData = JSON.stringify({
            "title": title,
            "time": time,
            "content": content,
            "tag": tag,
        });
        uploadDataFunc(uploadData);
        console.log(uploadData);
        alert('‰∏ä‰º†ÊàêÂäü');
        setContent('');
        setTitle('');
        setTime('');
        setTag('');
    }

    return (
        <div className="home-container">
            <NavBar />
            <div className="input-container">
                <TextField
                    className="home-title"
                    label="Ê†áÈ¢ò"
                    variant="filled"
                    onBlur={handleTitle}
                />
                <TextField
                    className="home-time"
                    label="Êó∂Èó¥"
                    variant="filled"
                    onBlur={handleTime}
                />
                <TextField
                className="home-tag"
                label="Ê†áÁ≠æ"
                variant="filled"
                onBlur={handleTag}
                />
                <textarea
                className="home-content"
                placeholder="ÂÜÖÂÆπ"
                onBlur={handleContent}
                />
                <Button
                    className="home-submit"
                    variant="contained"
                    onClick={handleSubmit}
                >Êèê‰∫§</Button>
            </div>
        </div>
    )
}