import * as React from 'react';
import { useState } from 'react';
import '../style/comment.css'
import defaultEvent from './SubAndPub';

function CommentInput({blogId}: {blogId: string}) {
    const [userName, setUserName] = useState('');
    const [content, setContent] = useState('');

    function handleUserNameChange(e:React.ChangeEvent<HTMLInputElement>):void {
        setUserName(e.target.value);
    }

    function handleContentChange(e:React.ChangeEvent<HTMLTextAreaElement>):void {
        setContent(e.target.value);
    }

    function handleSubmit() {
        const domUserName = document.getElementById('userName') as HTMLInputElement;
        const domContent = document.getElementById('content') as HTMLInputElement;
        var updateComments = new Date().getTime();
        var result = JSON.stringify({
            "userName": userName,
            "content": content
        });
        if(userName === '' || content === '') {
            alert('ËØ∑ËæìÂÖ•Áî®Êà∑ÂêçÂíåËØÑËÆ∫ÂÜÖÂÆπ');
            return;
        }
        fetch(`http://localhost:1234/api/postComments/?id=${blogId}`,{
            method: 'post',
            headers: {
                'Content-Type': 'application/json;charset=utf-8'
            },
            body: result
        });
        if(domUserName) {
            domUserName.value = '';
        }
        if(domContent) {
            domContent.value = '';
        }
        defaultEvent.emit("commentInput",updateComments);
    }

    return(
        <div className='comment-input-container'>
            <div className='comment-input'>
            <div className='comment-header'>
                <div className='comment-header-left'>
                    ÂßìÂêç:
                </div>
                <input 
                className='comment-header-right' 
                type='text'
                placeholder='ËØ∑ËæìÂÖ•‰Ω†ÁöÑÂêçÂ≠ó'
                id='userName'
                onChange={handleUserNameChange}/>
            </div>
            <div className='comment-body'>
                <div className='comment-body-left'>
                    ÂÜÖÂÆπ:
                </div>
                <textarea 
                className='comment-body-right' 
                id='content'
                onChange={handleContentChange}
                placeholder='ËØ∑ËæìÂÖ•‰Ω†ÁöÑËØÑËÆ∫'/>
            </div>
            <div className='submit'>
                <button className='submit-btn'
                onClick={handleSubmit}>
                    Êèê‰∫§
                </button>
            </div>
            </div>
        </div>
    )
};

export default CommentInput;